body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #bdbfc6;
}

.App {
  width: 60%;
  margin: 0 auto;
  font-family: Verdana, 'Lato', sans-serif;
}


ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  margin: 15px 0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  border-radius: 20px;
  border: 2px solid #4C2E05;
}

li span {
  padding: 8px;
}

li.low {
  background-color: #BCED09;
}
li.medium {
  background-color: #F9CB40;
}
li.high {
  background-color: #FF715B;
}
li.done {
  background-color: #f6f6ff9f;
}

input[type="text"] {
  padding: 10px;
  width: 50%;
  margin:5px;
}

button, input[type="submit"]{
  padding: 10px;
  background: #2f52e0ca;
  color: rgb(255, 255, 255);
  cursor: pointer;
  border: 1px solid #2F52E0;
  margin: 8px;
  border-radius: 10px;
}

ul > li > button:hover, #save-task:hover{
  color: #2f52e0;
  background-color: rgba(255, 255, 255, 0.812);
  transition: 0.2s;
}

form > button.remove:hover {
  background: #ff715b;
  border: 1px solid #4C2E05;
  color: black;
  transition: 0.2s;
}

form > button.reorder:hover {
  background: #BCED09ca;
  border: 1px solid #4C2E05;
  color: black;
  transition: 0.2s;
}

#new-task {
  border-radius: 10px;
  border: 1.5px solid #4C2E05;
}
